import Header from "./Header";
import data from "../../config.json";
import layoutStyles from "../styles/components/layout.scss";
import Router from 'next/router'
import {useState} from 'react'

export default function Layout(props) {
  // const isInfo = router.pathname === '/info'
  const [bgColor, setBgColor] = useState('inherit')
  const [typeColor, setTypeColor] = useState('inherit')
  function isInfoPage() {
    if(Router.pathname == "/info") {
      return true;
    } else {
      return false;
    }
  }
  function setColor() {
    if(isInfoPage()) {
      setBgColor(data.primary_color)
      setTypeColor("#fff")
    } else {
      setBgColor("inherit")
      setTypeColor("inherit")
    }
  }
  return (
    <section
      className={`${layoutStyles.layout} ${isInfoPage() &&
        layoutStyles.info_page}`}
      style={{
        backgroundColor: `${typeof window !== "undefined" &&
        Router.pathname == "/info" && 
          data.primary_color}`
      }}
    >
      <Header />
      <div className={layoutStyles.content}>{props.children}</div>
    </section>
  );
}


